table {
	border-collapse: collapse;
	border-spacing: 0;
	margin: $small-spacing 0;
	table-layout: fixed;
	width: 100%; }

th {
	border-bottom: $base-border;
	font-weight: 600;
	padding: $small-spacing 0;
	text-align: left; }

td {
	border-bottom: $base-border;
	padding: $small-spacing 0; }

tr, td, th {
	vertical-align: middle; }
