		&:focus {
			border-color: $color-warning;
			box-shadow: $form-box-shadow, 0 0 5px adjust-color($color-warning, $alpha: -0.3); } } }

.warning-state-bg {
	@extend .warning-state;
	background-color: lighten($yellow, 32%); }


